import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ETPM',
    meta: { title: 'ETPM日报' },
    component: () => import('@/views/ETPM/index'),
  },
  {
    path: '/lose',
    name: 'lose',
    meta: { title: '停机损失分析' },
    component: () => import('@/views/lose/index')
  },
  {
    path: '/spare',
    name: 'spare',
    meta: { title: '备件使用分析' },
    component: () => import(/* webpackChunkName: "about" */ '../views/spare-part.vue')
  },
  {
    path: '/test',
    name: 'test',
    meta: { title: 'test' },
    component: () => import('@/components/test'),
  }
]

const router = new VueRouter({
  // mode: 'history',
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta && to.meta.title)
    document.title = to.meta.title
  next()
})


export default router
