<template>
  <a-locale-provider id="app" :locale="locale">
    <l-layout>
      <l-layout-aside>
        <a-menu
          theme="dark"
          class="menu"
          v-model="defaultSelectedKeys"
          @click="click"
        >
          <a-menu-item v-for="(v, i) in menus" :key="v.name">{{
            v.text
          }}</a-menu-item>
        </a-menu>
      </l-layout-aside>
      <l-layout-content class="content">
        <router-view />
      </l-layout-content>
    </l-layout>
  </a-locale-provider>
</template>

<script>
import zhCN from "ant-design-vue/lib/locale-provider/zh_CN";
export default {
  data() {
    return {
      locale: zhCN,
      menus: [],
      defaultSelectedKeys: []
    };
  },
  mounted() {
    let menus = this.$router.options.routes.map(v => {
      return {
        text: v.meta && v.meta.title ? v.meta.title : v.name,
        name: v.name
      };
    });
    this.menus = menus;
    this.defaultSelectedKeys = [menus[0].name];
  },
  methods: {
    click(item) {
      this.$router.push({ name: item.key });
    }
  }
};
</script>
<style scoped>
#app {
  /* height: 100%; */
}
.menu {
  height: 100%;
  padding: 20px 0;
}
.content {
  background-color: #f4f4f4;
}
</style>
