
const components = [
    'select',
    'form'
], componentsObj = {
    'layout': {
        template: `<div class="l-layout" :class="{'l-layout-has-aside':hasAside}"><slot></slot></div>`,
        data() {
            return {
                hasAside: false
            }
        },
        mounted() {
            this.hasAside = this.$children.some(v => {
                let className = v.$el.className || ''
                return className.split(' ').some(m => m === 'l-layout-aside')
            })
        }
    },
    'layout-content': { template: '<div class="l-layout-content"><slot></slot></div>' },
    'layout-aside': {
        props: { width: { type: String, default: '200px' } },
        template: `<div class="l-layout-aside" :style="'width:'+width"><slot></slot></div>`,
    },
    'layout-header': { template: '<div class="l-layout-header"><slot></slot></div>' },
    'layout-footer': { template: '<div class="l-layout-footer"><slot></slot></div>' },
    'breadcrumb': { template: `<a-breadcrumb class="l-breadcrumb"><slot></slot></a-breadcrumb>` },
    'tag': {
        props: { color: { type: String, default: '' } },
        template: `<a-tag class="l-tag" :color="color"><slot></slot></a-tag>`
    },
}

import "./less/index.less"
import Antd from 'ant-design-vue';

function install(Vue) {
    Vue.use(Antd);
    for (let [key, params] of Object.entries(componentsObj)) {
        Vue.component(`l-${key}`, typeof params === 'string' ? () => import(`./components/${params}.vue`) : params);
    }
    components.map(function (component) {
        Vue.component(`l-${component}`, () => import(`./components/${component}.vue`));
    });
}

export default install